@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700&display=swap);
* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

.App {
  display: -webkit-flex;
  display: flex;
  background-color: #22252d;
  min-width: 100vw;
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  overflow: hidden;
}
.overview {
  width: 100%;
  margin: 30px;
  background-color: #2e3139;
  border-radius: 15px;
}
.bars-1 {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 45px;
  height: 40px;
  --c: linear-gradient(currentColor 0 0);
  background: var(--c) 0% 50%, var(--c) 50% 50%, var(--c) 100% 50%;
  background-size: 9px 100%;
  background-repeat: no-repeat;
  -webkit-animation: b1 1s infinite linear;
          animation: b1 1s infinite linear;
}
@-webkit-keyframes b1 {
  33% {
    background-size: 9px 10%, 9px 100%, 9px 100%;
  }
  50% {
    background-size: 9px 100%, 9px 10%, 9px 100%;
  }
  66% {
    background-size: 9px 100%, 9px 100%, 9px 10%;
  }
}
@keyframes b1 {
  33% {
    background-size: 9px 10%, 9px 100%, 9px 100%;
  }
  50% {
    background-size: 9px 100%, 9px 10%, 9px 100%;
  }
  66% {
    background-size: 9px 100%, 9px 100%, 9px 10%;
  }
}

.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 200px;
  background-color: #22252d;
  color: rgb(255, 255, 255);
  position: relative;
}
.navbar h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  padding: 40px 30px;
}
.nav-links {
  list-style: none;
  padding: 0;
  margin-top: 5vh;
}
.nav-links li button a {
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  color: rgb(255, 255, 255);
}
.btn {
  width: 170px;
  height: 50px;
  border-radius: 9px;
  background: #485563; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #29323c,
    #485563
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.btn i {
  margin: 0 5px;
}

.navbar img {
  width: 57px;
  height: 57px;
  position: absolute;
  bottom: 30px;
  left: 30px;
  border-radius: 50%;
  box-shadow: 0 10px 50px -6px #22252d;
}

.district {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 4vh 10px 0 10px;
  font-family: "Montserrat", sans-serif;
}
.district > div h2 {
  font-family: "Montserrat", sans-serif;
  color: rgb(245, 237, 237);
  margin-left: 30px;
}
.district > div > h2 > span {
  font-weight: 400;
}
.selectors {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
select {
  -webkit-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #5c6664;
  background-image: none;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 0 0.5em;
  color: #fff;
  cursor: pointer;
  font-size: 1em;
  font-family: "Montserrat", sans-serif;
}
select::-ms-expand {
  display: none;
}
.select {
  position: relative;
  display: -webkit-flex;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #5c6664;
  overflow: hidden;
  border-radius: 0.25em;
  margin: 0 20px;
}
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #2b2e2e;
  color: #ffff;
  cursor: pointer;
  pointer-events: none;
  transition: 0.25s all ease;
}
.select:hover::after {
  color: #23b499;
}

.cards {
  margin: 3vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-family: "Montserrat", sans-serif;
}
.card {
  height: 160px;
  width: 300px;
  text-align: center;
  margin: 10px;
  border-radius: 12px;
  background-color: #22252d;
  color: #fff;
  box-shadow: 0px 0px 24px #22252d;
  display: -webkit-flex;
  display: flex;
}
.content {
  width: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.icon {
  margin: 14px;
  width: 45px;
  height: 45px;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.icon i {
  font-size: 1.6rem;
}
.cards div:nth-child(1) .icon {
  background-color: rgb(255, 7, 58);
}
.cards div:nth-child(2) .icon {
  background-color: rgb(40, 167, 69);
}
.cards div:nth-child(3) .icon {
  background-color: rgb(108, 117, 125);
}
.count {
  margin-left: 14px;
}
.count p {
  color: #4f5052;
  margin-bottom: 5px;
  margin-left: 0;
}
.change {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.increase {
  display: -webkit-flex;
  display: flex;
  margin: 14px 24px 0 0;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  width: 90%;
}
.red {
  color: rgb(250, 3, 3);
}
.green {
  color: rgb(0, 255, 0);
}
.change > p {
  color: #4f5052;
  text-align: end;
  margin: 5px 14px 0 0;
  font-size: 0.8rem;
}
.noChart {
  margin-left: 5px;
  margin-top: 5px;
  width: 85%;
  height: 80px;
}

.LineChart {
  display: -webkit-flex;
  display: flex;
}
@media only screen and (max-width: 1277px) {
  .LineChart {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .chart {
    min-width: 90%;
    margin-bottom: 20px;
  }
}
.chart {
  width: 37vw;
  height: 340px;
  background-color: #22252d;
  margin-left: 45px;
  border-radius: 8px;
  padding: 15px;
}

